.header {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 100;
    background: #fff;
    padding-bottom: 10px;
}
a {
    text-decoration: none;
}
button {
    cursor: pointer;
}
.dialog .MuiPaper-root {
    width: 100% !important;
    height: 100% !important;
    border-radius: 0;
}
.dialog.select .MuiPaper-root {
    width: 80% !important;
    height: 60% !important;
    border-radius: 0;
}
.dialog.full_screen .MuiPaper-root {
    width: 85% !important;
    height: 85% !important;
    border-radius: 0;
}
.dialog.carousel .MuiPaper-root {
    width: 420px !important;
    height: 550px !important;
}
.dialog.terms-dialog .MuiPaper-root {
    min-width: 60% !important;
    height: auto !important;
    border-radius: 10px;
    border: 4px solid rgba(0, 0, 0, .87);
    padding-bottom: 20px;
}
.slick-slider {
    height: 100% !important;
}
.slick-list {
    height: 100% !important;
}
.slick-track {
    height: 100% !important;
}

.slick-prev .slick-next{
background: white;
width: 30px;
height: 30px;
}

.slick-next:hover{
    background: white;
    width: 30px;
    height: 30px;
}

.slick-prev:hover{
    background: white;
    width: 30px;
    height: 30px;
}

.slick-prev::before{
    background-image: url(/static/media/left_arrow.e111068e.png);
    /* background-size: 25px 20px; */
    background-size: 10px 20px;
    display: inline-block;
    width: 10px; 
    height: 20px;
    content: "";
}

.slick-next::before{
    background-image: url(/static/media/right_arrow.db95360d.png);
    background-size: 10px 20px;
    display: inline-block;
    width: 10px; 
    height: 20px;
    content:"";
}

.slick-arrow.slick-prev {
    left: 12px !important;
    z-index: 2;
}
.slick-arrow.slick-next {
    right: 12px !important;
}
@media (max-width: 576px) {
    .dialog.select .MuiPaper-root {
        margin: 10px;
        width: 100% !important;
        height: -webkit-max-content !important;
        height: max-content !important;
        border-radius: 0;
    }
    .dialog.full_screen .MuiPaper-root {
        width: 100% !important;
        height: 300px !important;
        border-radius: 0;
        /* margin: 20px 0; */
        margin: 20px 5%;
    }
    .dialog .MuiPaper-root {
        margin: 0;
    }
    .dialog.carousel .MuiPaper-root  {
        margin: 10px;
        height: 560px !important;
    }
}

.header_container {
    padding: 15px 20px 0;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header_container:after {
    height: 20px;
    width: 100%;
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    background-image: url(data:image/png;base64,);
    background-position: center;
    background-size: contain;
    z-index: -1;
}
.header_container img {
    height: 100px;
}
.header_container img.menu {
    width: 35px;
    height: 30px;
    cursor: pointer;
}
.side_menu {
    position: fixed;
    width: 300px;
    height: calc(100vh - 50px);
    right: 0;
    bottom: 0;
    background: #555;
    color: #fff;
    padding: 20px;
    box-sizing: border-box;
}
.side_menu.in {
    -webkit-animation: animateIn .5s ease-in-out;
            animation: animateIn .5s ease-in-out;
}
.side_menu.out {
    -webkit-animation: animateOut .5s ease-in-out;
            animation: animateOut .5s ease-in-out;
}
.side_menu span {
    display: block;
    cursor: pointer;
    text-align: right;
}
.side_menu span.share {
    display: block;
    text-align: left;
    margin-left: 10px;
}
.side_menu span i {
    font-size: 26px;
}
.side_menu span.share i {
    font-size: 34px;
}
.side_menu h5 {
    font-size: 22px;
    color: #fff;
    font-weight: 600;
    padding: 15px 0;
    margin: 0 0 15px;
}
.side_menu p {
    font-size: 17px;
    color: #fff;
    font-weight: normal;
    padding: 10px 0;
    margin: 0 10px;
}

@-webkit-keyframes animateIn {
    0% {
        opacity: 1;
        right: -300px;
    }
    100% {
        opacity: 1;
        right: 0;
    }
}

@keyframes animateIn {
    0% {
        opacity: 1;
        right: -300px;
    }
    100% {
        opacity: 1;
        right: 0;
    }
}
@-webkit-keyframes animateOut {
    0% {
        opacity: 1;
        right: 0;
    }
    100% {
        opacity: 1;
        right: -300px;
    }
}
@keyframes animateOut {
    0% {
        opacity: 1;
        right: 0;
    }
    100% {
        opacity: 1;
        right: -300px;
    }
}
